.profileSection {
  display: flex;
  align-items: center;
  margin: 0 8px;
}

.profileImage {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #e0e0e0;
  transition: all 0.3s ease;
}

.profileImage:hover {
  border-color: #6366f1;
  transform: scale(1.05);
}

.profileCircle {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 600;
  font-size: 16px;
  border: 2px solid #e0e0e0;
  transition: transform 0.3s ease;
}

.profileCircle:hover {
  transform: scale(1.05);
}
